.home_container{
   width: 100%;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   padding: 4em;
}

.home_container h1{
   font-size: 2.5em;
   margin-bottom: .5em;
}

.home_container h1 span{
   color: #deb054;
   padding: 0 .2em;
   background-color: #2b2a2a;
}

.home_container p{
   margin-bottom: 1.5em;
   color: rgb(33, 33, 33);
}

.home_container img{
   width: 350px;
   margin: 2em 0
}



 @media (max-width: 667px) {
    .home_container {
     margin-left: 20rem;
   }
 }